@import "../../styles/variables";

.filterBlock {
  display: flex;
  justify-content: flex-start;
  gap: 0 22px;
  width: 100%;
  @include block;
  padding: 12px 27px;
  input,
  select {
    position: relative;
    height: 37px;
    background: #f9fafb;
    border: 1px solid #dedee0;
    box-sizing: border-box;
    border-radius: 5px;
    font-size: 14px;
    line-height: 26px;
    color: $textGrey;
    padding: 0 34px;
    width: 100%;
  }
}
.select,
.search {
  position: relative;
  width: 290px;
  cursor: pointer;
}
.select.sort {
  width: 182px;
}
.search {
  i {
    position: absolute;
    display: flex;
    left: 8px;
    top: 50%;
    transform: translateY(-50%);
  }
}

select {
  appearance: none;
  outline: none;
  background-image: none;
  &::ms-expand {
    display: none;
  }
  option {
    background: #fff;
    &:hover {
      background: green;
    }
  }
}

.selectInput {
  color: green;
}

@media (max-width: 780px) {
  .filterBlock {
    flex-wrap: wrap;
  }
  .search {
    width: 100%;
    margin-bottom: 20px;
  }
  .select {
    flex: 1;
  }
}

@media (max-width: 520px) {
  .filterBlock {
    flex-direction: column;
  }
  .select {
    width: 100%;

    &.sort {
      margin-top: 20px;
      width: 100%;
    }
  }
}
