@import "../../../styles/variables";

.purchase {
  background: #ffffff;
  box-shadow: 0px 2px 42px rgba(0, 0, 0, 0.111233);
  border-radius: 7px;
  padding: 69px 87px 51px;
  h2 {
    color: $header;
    font-weight: 500;
    font-size: 24px;
    line-height: 35px;
    margin: 10px;
    white-space: nowrap;
  }
  p {
    font-size: 15px;
    line-height: 22px;
    text-align: center;
    margin-bottom: 38px;
  }
  button {
    margin-bottom: 18px;
  }
}

.purchaseContent {
  max-width: 310px;
  width: 100%;
  text-align: center;
}
